import { Injectable } from '@angular/core';
import { HttpClient,HttpHeaders } from '@angular/common/http';
import { Contact } from './contact';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { Vendor } from '../vendor/vendor';

@Injectable({
  providedIn: 'root'
})
export class ContactService {

  constructor(private httpservice:HttpClient) { }

  getAllContacts(id:any): Observable<Contact[]>{
      return this.httpservice.get<Contact[]> (environment.APIUrl+'/listofcontact/'+id);      
    
  }
  disableById(id:any):any{
    let body = JSON.stringify(id);
    console.log(id);
    let headers = new HttpHeaders({'Content-Type': 'application/json'});
    let options = { headers: headers }
    return this.httpservice.put<Contact[]>(environment.APIUrl+'/disablecontact/'+id,body,options);
  }
}
