import { Component, OnInit } from '@angular/core';
import { Vendor } from './vendor';
import { VendorService } from './vendor.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-vendor',
  templateUrl: './vendor.component.html',
  styleUrls: ['./vendor.component.scss']
})
export class VendorComponent implements OnInit {

  vendor :Vendor[];
  v = new Vendor();
  searchText;
  searchid:any;
  constructor(private service:VendorService,private router:Router) { }

  ngOnInit() {
    this.getVendors();
  }
  
 edit(id:any){
   this.router.navigate(['addvendor',id])
 }
  getVendors():void{
    this.service.getAllVendors()
    .subscribe((data)=>{
      this.vendor=data;
     console.log(data);
    });
  }
  add(){
    this.router.navigate(['addvendor'])
  }
  logout(){
    this.router.navigate([''])
  }
  contact(id:any){
    this.router.navigate(['contact',id])
  }
  pass(search:any):void{
  this.searchid=search.vendorId;
  }
  disableVendor(){
    this.service.disableById(this.searchid)
    .subscribe((data)=>{
      this.contact=data;
      this.getVendors();
    })
  }
}
