import { Component, OnInit } from '@angular/core';
import { LoginService } from './login.service';
import { Router } from '@angular/router';
import { Login } from './login';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  Login=new Login();
  roleid: number;
  sessionTokenUserName: string;
  sessionTokenRoleID: string;
  
  constructor(private service:LoginService,private router:Router) { }

  ngOnInit() {
    this.resetForm()
  }
  OnSubmit(form:NgForm){
    this.validLogin(form);
  }
  resetForm(form?:NgForm){
    if(form!=null)
    form.resetForm();
    this.service.formData={
     
      
      userId:'',
      passWord:'',
      userName:''
                                                
    }                                   
  }
  validLogin(form:NgForm){
    this.service.validate(form.value).subscribe((data)=>{
      this.Login=data;
      console.log(data);
      var username = this.Login.userName;
      var password = this.Login.passWord;
      localStorage.setItem('isLoggedIn', "true");
      localStorage.setItem('token', data.user_id);
      localStorage.setItem('tokenRoleId', data.user_id);
      this.roleid = data.user_id;
     if(data){
       this.router.navigate(['/vendor']);
     }
    });
  }

}
