import { Component, OnInit } from '@angular/core';
import { Vendor } from '../vendor';
import { Router, ActivatedRoute } from '@angular/router';
import { AddvendorService } from './addvendor.service';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-add-vendor',
  templateUrl: './add-vendor.component.html',
  styleUrls: ['./add-vendor.component.scss']
})
export class AddVendorComponent implements OnInit {

  vendors:Vendor[];
  vendor=new Vendor();
  vendorId: any;

  constructor(private router: Router,private service: AddvendorService, private route:ActivatedRoute) { }

  ngOnInit() {
    this.getStaffById()
  }
  addVendor():void{

    this.service.insertVendor(this.vendor)
    .subscribe((response) => {
      console.log(response);
      
       this.getVendor();
      this.router.navigate(['vendor'])
      this.resetForm();
    },(error) => {
      console.log(error);
    });
  }
  resetForm(form?:NgForm){
    if(form!=null)
    form.resetForm();
    this.service.formData={
      
      vendorId: '',
      vendorName:'',
      vendorLoc: '',
      vendorAddr: '',
      service: '',
      pincode: '',
      isActive: '',
      contactId: '',
      name:'',
      department: '',
      email: '',
      phone : ''           
    }                                  
  }
  getVendor():void{
    this.service.getAllVendors()
    .subscribe((data)=>{
      this.vendors=data;
      console.log(data);

    },(error)=>{
      console.log(error);

    });
  }

  getStaffById(){
    this.vendorId=this.route.snapshot.paramMap.get('id')
    console.log(this.vendorId);
    this.service.getVendorById(this.vendorId)
    .subscribe((vendorbyId)=>{
      this.vendor=vendorbyId;
      console.log(vendorbyId);
    },(error)=>{
      console.log(error);
    });
  }
  backToStaff(){
    this.router.navigate(['vendor'])
  }
  logout(){
    this.router.navigate([''])
  }
}


