import { Component, OnInit } from '@angular/core';
import { Contact } from './contact';
import { ContactService } from './contact.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {

  contact:Contact[]
  c=new Contact();
  searchText;
  id:any
  cid:any;
  searchid:any;
  constructor(private service:ContactService,private router:Router,private route:ActivatedRoute) { }

  ngOnInit() {
    this.getContacts();
  }
  getContacts():void{
    this.cid=this.route.snapshot.paramMap.get('id')
    this.service.getAllContacts(this.cid)
    .subscribe((data)=>{
      this.contact=data;

    });

  }
  logout(){
    this.router.navigate([''])
  }
  pass(search:any):void{
    this.searchid=search.contact_id;
    }
    disableVendor(){
      this.service.disableById(this.searchid)
      .subscribe((data)=>{
        this.contact=data;
        this.getContacts();
      })
    }
    toAdmin(){
      this.router.navigate(['vendor'])
    }
}
